.wrapper {
  width: 100%;
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: #f2f2f2;

  .dev{
    font-size: 18px;
    font-weight: 700;
    position: absolute;
    bottom: 4%;
    right: 4%;
    pointer-events: none;
    color: rgb(73, 73, 73);
  }

  .card_wrapper {
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: url("../public/images/background.png");
    background-repeat: repeat;
    background-position: center;

    &.disabled {
      pointer-events: none;
    }

    .card_board {
      width: 70vw;
      max-width: 70vh;
      aspect-ratio: 1/1;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 20px;
      padding: 15px;
      box-shadow: 10px 12px 10px #d3d3d3;
      border-radius: 15px;

      background: linear-gradient(160deg, #02ccba 0%, #aa7ecd 100%);
      box-shadow: 12px 15px 20px rgba(46, 61, 73, 0.5);

      .card {
        cursor: pointer;
        border-radius: 10px;
        background-color: transparent;
        display: grid;
        place-items: center;
        user-select: none;

        &.flipped {
          pointer-events: none;
        }

        &.flipped .card_inner {
          transform: rotateY(180deg);
        }

        .card_inner {
          position: relative;
          width: 100%;
          height: 100%;
          transform-style: preserve-3d;
          transition: 0.6s cubic-bezier(0.38, 0.02, 0.09, 1.66) all;

          .front,
          .back {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 8px;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            backface-visibility: hidden;
            padding: 20px;
          }

          .front {
            background: #fff;
            transform: rotateY(180deg);
            box-shadow: -3px -3px 6px #536270c2;

            img {
              border-radius: 8px;
            }
          }

          .back {
            background: #2f3d4a;
            box-shadow: 3px 3px 6px #536270c2;
            transition: 0.3s ease all;
          }

          .back:hover {
            background: #374c5f;
            transform: rotate(2deg) scale(1.03);
          }
        }
      }
    }

    .player {
      position: absolute;
      top: 5%;
      z-index: 1000;
      padding: 12px 20px;
      background: linear-gradient(160deg, #02ccba 0%, #aa7ecd 100%);
      box-shadow: 12px 15px 20px rgba(46, 61, 73, 0.5);
      border-radius: 5px;
      font-weight: 600;
      font-size: 23px;
      text-transform: capitalize;
      color: #fff;
      display: flex;
      align-items: center;
      gap: 5px;
      overflow: hidden;

      &.one {
        left: 5%;
      }
      &.two {
        right: 5%;
      }

      &::before {
        content: "";
        display: block;
        position: absolute;
        background: rgba(255, 255, 255, 0.5);
        width: 60px;
        height: 100%;
        top: 0;
        filter: blur(30px);
        transform: translateX(-100px) skewX(-15deg);
      }
      &::after {
        content: "";
        display: block;
        position: absolute;
        background: rgba(255, 255, 255, 0.2);
        width: 30px;
        height: 100%;
        top: 0;
        filter: blur(5px);
        transform: translateX(-100px) skewX(-15deg);
      }

      &.hisTurn {
        &::before,
        &::after {
          // transition: 0.7s;
          animation: shine 0.7s linear infinite;
        }
      }

      &.hisTurn {
        animation: scale 0.7s linear infinite;
      }

      span {
        width: 35px;
        display: grid;
        background: black;
        border-radius: 4px;
        place-items: center;
      }
    }

    .winner_container {
      width: 100%;
      height: 100%;
      position: absolute;
      inset: 0;
      z-index: 1000;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      gap: 50px;
      background: url("../public/images/background.png");
      overflow: hidden;
      animation: reveal 0.5s ease-out forwards;
      transform: scale(0.5);
      opacity: 0;

      h1 {
        font-size: 50px;
        font-weight: 700;
        text-transform: uppercase;
        background: linear-gradient(160deg, #02ccba 0%, #aa7ecd 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

        span {
          font-size: 60px;
        }
      }
      a {
        font-size: 25px;
        background: linear-gradient(160deg, #02ccba 0%, #aa7ecd 100%);
        padding: 10px 20px;
        border-radius: 50px;
        color: #fff;
        border: 5px solid rgb(255, 255, 255);
        box-shadow: 3px 3px 6px #536270c2;
      }

      .ballons,
      .stars {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: url("../public/images/balloons.png");
        background-repeat: repeat;
        background-size: 200px;
        background-position: center;
        pointer-events: none;
        transition: 4s;
        transform: translateY(100%);
        opacity: 0;
        animation: ballons 1.5s 1s linear forwards;
      }
      .stars {
        background: url("../public/images/stars.png");
        transition: 5s;
      }
    }
  }

  .start_container {
    width: 100%;
    height: 100%;
    background: url("../public/images/background.png");
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    gap: 30px;

    h1 {
      font-size: 55px;
      background: linear-gradient(160deg, #02ccba 0%, #aa7ecd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      z-index: 2;
    }
    .first {
      margin-bottom: 40px;
    }
    .player_details {
      font-size: 25px;
      font-weight: 600;

      .form__group {
        position: relative;
        padding: 15px 0 0;
        margin-top: 10px;
        margin-bottom: 20px;
      }
      .form__field {
        font-family: inherit;
        width: 335px;
        border: 0;
        border-bottom: 2px solid #9b9b9b;
        outline: 0;
        font-size: 1.3rem;
        padding: 7px 0;
        background: transparent;
        transition: border-color 0.2s;

        &::placeholder {
          color: transparent;
        }
        &:placeholder-shown ~ .form__label {
          font-size: 1.3rem;
          cursor: text;
          top: 20px;
        }
      }
      .form__label {
        position: absolute;
        top: 0;
        display: block;
        transition: 0.2s;
        font-size: 1rem;
        color: #9b9b9b;
        pointer-events: none;
      }

      .form__field:focus {
        ~ .form__label {
          position: absolute;
          top: 0;
          display: block;
          transition: 0.2s;
          font-size: 1rem;
          color: #11998e;
          font-weight: 700;
        }
        padding-bottom: 6px;
        font-weight: 700;
        border-width: 3px;
        border-image: linear-gradient(160deg, #02ccba 0%, #aa7ecd 100%);
        border-image-slice: 1;
      }
      /* reset input */
      .form__field {
        &:required,
        &:invalid {
          box-shadow: none;
        }
      }
    }
    a {
      font-size: 22px;
      background: linear-gradient(160deg, #02ccba 0%, #aa7ecd 100%);
      padding: 10px 20px;
      border-radius: 50px;
      color: #fff;
      border: 5px solid rgb(255, 255, 255);
      box-shadow: 3px 3px 6px #536270c2;
    }

    &::after {
      content: "";
      background: url("../public/images/ribbins.png");
      background-position: center;
      background-size: 250px;
      background-repeat: repeat;
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 250px;
      animation: ribbins 1s ease-out alternate-reverse infinite;
    }
 
  }
}

@keyframes shine {
  0% {
    transform: translateX(-100px) skewX(-15deg);
  }
  100% {
    transform: translateX(300px) skewX(-15deg);
  }
}

@keyframes scale {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}

@keyframes ballons {
  to {
    opacity: 1;
    transform: translateY(-120%);
  }
}

@keyframes reveal {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes ribbins {
  to {
    transform: translate(-10px, -10px);
  }
}
